.App{display:flex;justify-content:center;height:100vh;position:relative}.wrapper{width:80%;overflow:scroll;padding-bottom:60px}.wrapper::-webkit-scrollbar{display:none}@media screen and (max-width: 1400px){.wrapper{width:90vw}}@media screen and (max-width: 1200px){.wrapper{width:100vw}}

header{display:flex;justify-content:center;align-items:center;padding:20px 0}.logo{height:40px}

.intro-container{display:flex;height:450px}.intro-box{width:33.33%;height:100%}.intro-box-left{background-image:url(/static/media/image_03.24ab5128.jpg);background-position:left;background-repeat:no-repeat;background-size:cover}.intro-box-middle{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;background-color:var(--gray-custom);padding:20px 0}.intro-box-right{background-image:url(/static/media/image_01.13e75e62.jpg);background-position:center;background-repeat:no-repeat;background-size:cover}.beeren-softgum-img{height:120px}.improving-health-img{height:20px;margin-bottom:60px}@media screen and (max-width: 1012px){.intro-container{flex-direction:column;height:1350px}.intro-box{width:100%;height:450px}.beeren-softgum-img{height:150px}.improving-health-img{height:30px;margin-bottom:60px}}@media screen and (max-width: 520px){.intro-container{flex-direction:column;height:1050px}.intro-box{width:100%;height:350px}.beeren-softgum-img{height:120px}.improving-health-img{height:20px;margin-bottom:60px}}

.order-btn{display:block;margin:0 auto;padding:10px 15px;border:1px solid var(--green-custom);border-radius:25px;background-color:var(--green-custom);color:var(--white-custom);font-size:15px;font-family:'TTCommons-Medium', Helvetica, sans-serif;text-transform:uppercase;letter-spacing:1px;transition:box-shadow .25s}.order-btn:hover{background-color:var(--green-custom);box-shadow:2px 3px 5px #474747;cursor:pointer}.order-btn:active{box-shadow:0 0 0 black}.order-btn:focus{outline:none}

.advertiser-container{display:flex;flex-direction:column;align-items:center;padding-top:20px;height:650px;width:100%;background-image:var(--myGradient)}@-webkit-keyframes blink{to{border-right:5px solid var(--pink-custom);border-bottom:5px solid var(--pink-custom)}}@keyframes blink{to{border-right:5px solid var(--pink-custom);border-bottom:5px solid var(--pink-custom)}}.innutri-desktop-img{width:800px;margin-bottom:-50px}@media screen and (max-width: 1200px){.advertiser-container{height:550px}.innutri-desktop-img{width:600px;margin-bottom:0px}}

.usp-wrapper{width:100%;background-color:var(--white-custom);display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;padding:60px 0}.usp-container{width:100%;display:flex;justify-content:space-evenly;margin-bottom:40px}.usp-box{display:flex;flex-direction:column;align-items:center;width:25%;padding:0 20px}.usp-box img{width:60px;margin-bottom:15px}.usp-box h3{margin-bottom:10px;text-transform:uppercase;letter-spacing:1px;font-size:16px;font-family:'TTCommons-Light', Helvetica, sans-serif}.usp-box p{text-align:center;font-family:'TTCommons-Light', Helvetica, sans-serif;font-size:18px}.usp-box span{text-decoration:underline}@media screen and (max-width: 1012px){.usp-container{flex-direction:column;width:50%;margin-bottom:0}.usp-box{width:100%;margin-bottom:40px}.usp-box:last-child{margin-bottom:60px}.usp-box h3{text-align:center}}@media screen and (max-width: 520px){.usp-container{flex-direction:column;width:75%;margin-bottom:0}.usp-box{width:100%;margin-bottom:40px}.usp-box:last-child{margin-bottom:60px}.usp-box h3{text-align:center}}

