footer{padding:60px 0 40px 0;display:flex;justify-content:center;align-items:center;width:100%}.footer-container{display:flex;justify-content:space-between;align-items:flex-start;width:80%}.footer-container p{margin-bottom:5px}.footer-address-box p,.footer-address-box a{font-family:'TTCommons-Light', Helvetica, sans-serif;font-size:20px}.footer-address-box a{text-decoration:none;border-bottom:1px solid transparent;color:black;transition:all .35s}.footer-address-box a:hover{border-bottom:1px solid black}.footer-impressum a{color:black;text-decoration:none;border-bottom:1px solid transparent;font-family:'TTCommons-Medium', Helvetica, sans-serif;font-size:18px;text-transform:uppercase;transition:all .35s}.footer-impressum a:hover{border-bottom:1px solid black}.footer-logo{height:40px}.footer-follow p{font-family:'TTCommons-Light', Helvetica, sans-serif;font-size:18px;text-transform:uppercase;letter-spacing:2px;margin-bottom:15px}.footer-follow img{height:50px;margin-right:20px;transition:all .35s}.footer-follow img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}@media screen and (max-width: 1012px){footer{padding-bottom:0}.footer-container{flex-direction:column;justify-content:space-between;align-items:center;width:75%}.footer-address-box{width:100%;order:3;margin-bottom:40px}.footer-address-box p,.footer-address-box a{text-align:center}.footer-address-box a{display:block}.footer-impressum{order:3;margin-bottom:40px}.footer-logo{order:4}.footer-follow{width:100%;order:1;display:flex;flex-direction:column;align-items:center;margin-bottom:40px}.footer-follow p{margin-bottom:20px}.icon-box{width:60%;display:flex;justify-content:space-around}.icon-box img{margin:0}}@media screen and (max-width: 520px){.footer-container{width:100%}.footer-follow p{font-size:16px}.footer-address-box p,.footer-address-box a{font-size:18px}.icon-box img{height:40px}}

